.q_banner{

    background: url(../images/qa.png) no-repeat;
    background-size:center;
    background-position:center;
    height: 304px;
    position: relative;
    font-weight: bold;
    p{
        position: absolute;
        top: 214px;
        // bottom: px;
        font-size: 64px;
        line-height: 80px;
    position: relative;
    }
    p::after{
        content: "  ";
        background: url(../images/help-circle-sharp.svg) no-repeat ;
        background-size: cover;
        width: 58px;
        height: 58px;
        position: absolute;
        left:280px;
        top: 8px;
    }
    @include pad{
        
        p{  
            top: 239px;    
            font-size: 48px;
        }
        p::after{
            width: 48px;
            height: 48px;
            left: 208px;
            top: 12px;
        }
        margin-bottom: 40px;
    }
    @include phone{
        height: 143px;
        background-size:cover;
        margin-bottom: 24px;
    
            p{  
                top: 88px;    
                font-size: 28px;
            }
            p::after{
                width: 24px;
                height: 24px;
                left: 120px;
                top: 26px;
            }
    }
}
.q_list{
    font-weight: bold;
    padding-bottom: 264px;
    h2{
        font-size: 32px;
        line-height: 63px;
        margin-bottom: 36px;
    }
    .question{
        h3{
            font-size: 20px;
            line-height: 23px;
            margin-bottom: 16px;
        }
        p{

            padding: 0 0 40px 95px;
        } 
    }
    @include pad{
        padding-bottom: 64px;
        .question{
            p{
    
                padding-left: 60px;
            } 
        }
    }
    @include phone{
        padding-bottom: 36px;
        h2{
             font-size: 20px;
             line-height: 28px;
            margin-bottom: 12px;

        }

        .question{
            h3{
                font-size: 16px;
                line-height: 24px;
                margin-bottom: 8px;
            }
            p{
                font-size: 14px;
                line-height: 21px;
                padding-left: 60px;
                padding-bottom: 20px;
            } 
        }
    }
}
