/* 首頁 */
.h2{
    font-size: 48px;   
    line-height: 72px;   
    text-align: center;
    padding: 60px 0 64px;
    @include pad{
        font-size: 32px;
        line-height: 48px;
        padding: 40px 0 32px;
    }
    @include phone{
        font-size: 24px;
        line-height: 48px;
        padding: 32px 0 20px;
        width: 100%;
    }
}
.twoside_padding{
    padding-right: 39px;
    padding-left: 39px;
    @include phone{
        padding-right: 15px;
        padding-left: 15px;
    }

}
/* banner */
.banner{
    background: url(../images//banner.png) no-repeat;
    background-size: cover;
    background-position: center;
    height: 840px;
    .slogan{
        display: flex;
        flex-direction: column;
        height: 840px;
        align-items:flex-end;
        justify-content: center;
        font-size: 36px;
        line-height: 54px;
    }
    @include pad{
        background: url(../images//banner-sm.png) no-repeat;
        background-position: center;
        background-size: cover;
        .slogan{
            font-size: 20px;
            line-height: 30px;
            p{
                font-size: 48px;
                line-height: 72px;
            }
        }
    }
    @include phone{
        padding-bottom: 72px;
        height: 394px;
        .slogan{
            font-size: 16px;
            line-height: 30px;
            height: 394px;
            p{
                font-size: 32px;
                line-height: 72px;
            }
        }
    }
}

/* 優點 */
.advantage{
    padding: 0 85px 80px;
    .advantage_list{
        align-items: flex-start;
        flex-wrap: wrap;
        .advantage_img{
            display: flex;
            justify-content: center;
            align-items: flex-start;
            height: 100px;
            margin-bottom: 40px;
        }
        li{
            width: 18.74%;
        }
        h3{
            text-align: center;
            font-size: 24px;
            line-height: 36px;
            color: #650300;
        
        }
    }
    @include pad{
        padding: 0 85px;
        .advantage_list{
            li{
                width: 39.7%;
                margin-bottom: 40px;
            }
            h3{
                font-size: 20px;
                line-height: 30px;
            }
            .advantage_img{
                height: 80px;
                margin-bottom: 16px;
                img{
                    width: 80px;
                }
                
            }
        }
    }
    @include phone{
        padding-bottom: 12px;
        .advantage_list {
            justify-content: center;
            li{
                width: 63.2%;
            }
        }
    }
}

/* 經典系列鏡框 */
.classical_glass{
    
    .classical_temple{
       
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 32px;
        color: #AA0601;
        font-size: 48px;   
        line-height: 72px;
        font-family: Roboto;
        font-style: italic;
        .classical_img{
            max-width: 57.21%;
            
        }
    }
    .classical_temple:nth-child(2){
        flex-direction: row-reverse;
    }

    @include pad{
        padding-left: 39px;
        padding-right: 39px;
        .classical_temple{
            justify-content: flex-start;
            margin-bottom: 8px;
            font-size: 28px;   
            line-height: 42px;
            .classical_img{
                max-width: 56.52%;
                
            }
            .classical_word{
                margin-left: 30px;
                h3{
                    margin-left: 8px;
                }
                img{
                    max-width: 32px;
                
                }
            }
        }
        .classical_temple:nth-child(2){
            flex-direction: row;
        }
    }
    @include phone{
        padding: 0 15px;
        margin-bottom: 24px;
        img{
            display: none;
        }
        .classical_temple{
            justify-content: center;
            color: white;
        }
        li{
            width: 100%;
            height: 200px;
            .classical_word{
                position: relative;
            }
        }
        li:nth-child(1){
            background: url(../images//product-1.png);
            background-size: cover;
            background-position: center;     
            h3::before{
                content:'' ;
                position: absolute;
                color:none;
               background: url(../images//icon-diamond-white.svg) no-repeat;
               background-position: cover;
               width: 32px;
               height: 25.16px;
               top: 10px;
               left: -32px;
               
            }
        }
        li:nth-child(2){
            background: url(../images//product-2.png);
            background-size: cover;
            background-position: center;
            h3::before{
                content:'' ;
                position: absolute;
                color:none;
               background: url(../images//icon-sun-white.svg) no-repeat;
               background-position: cover;
               width: 32px;
               height: 32px;
               top: 3px;
               left: -32px;
               
            }
  
        }
        li:nth-child(3){
            background: url(../images//product-3.png);
            background-size: cover;
            background-position: center;
            h3::before{
                content:'' ;
                position: absolute;
                color:none;
               background: url(../images//icon-thunder-white.svg) no-repeat ;
               background-position: cover;
               width: 23px;
               height: 32px;
               top: 3px;
               left: -23px; 
            }
            
        }
    }
}

/* 聯名設計鏡框 */
.cowork{
    background: url(../images//bg.png) no-repeat;
    background-position: center;
    background-size: cover;
    height: 743px;
    .cowork_content li{
        width: 48.65%;
        height: 472px;
    }
    .cowork_content li:nth-child(1){
        background: url(../images//product-8.png)  no-repeat;
        background-position: center;
        background-size: cover;
    
    
    }
    .cowork_content li:nth-child(2){
        background: url(../images//product-9.png)  no-repeat;
        background-position: center;
        background-size: cover;
    }
    .cocowork_content_p{
        width: 100%;
        height: 100%;
        background: rgba(255, 255, 255, 0.5);
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: Roboto;
        font-size: 72px;
        line-height: 108px;
        color: #AA0601;
    }
    @include pad{
        height: 437px;
        .cowork_content li{
            height: 280px;
        }
        .cowork_content p{ 
            font-size: 32px;
            line-height: 25px;
        }
    }
    @include phone{
        height: 696px;
        padding-bottom: 16px;
        .cowork_content{
            flex-wrap: wrap;
            li{
                width: 100%;
                margin-bottom: 16px;
            
            }
        }
        .cowork_content li{
            height: 284px;
        }
    }
}




/* 顧客推薦 */
.customer_recommand{
    padding-right: 20px;
    padding-left: 20px;
    .recommand{
        /* align-items: flex-start; */
        flex-wrap: wrap;
        font-family: Roboto;
        font-weight: normal;
        li{
            width: 22.97%;
            img{
                width: 100%;
            
            }
            .recommand_p{
                border: 1px solid #DEE2E6;
                padding: 14px;
                h3{
                    font-size: 14px;
                    line-height: 21px;
                    margin-bottom: 7px;
                }
            }
            .recommand_p:nth-child(2){
                margin-bottom: 10px;
               
            }
            .recommand_p:last-child{
                color: #707070;
                font-size: 14px;
                line-height: 21px;
            }
        }
    }
    @include pad{
        padding-left: 39px;
        padding-right: 39px;
        .recommand{
            li{
                width: 47.83%;
                margin-bottom: 28px;
                img{
                    width: 100%;
                }
                .recommand_p{
                    border: 1px solid #DEE2E6;
                    padding: 14px;
                    h3{
                        font-size: 14px;
                        line-height: 21px;
                        margin-bottom: 7px;
                    }
                }
                .recommand_p :nth-child(2){
                    margin-bottom: 10px;
                   
                }
                .recommand_p :last-child{
                    color: #707070;
                    font-size: 14px;
                    line-height: 21px;
                }
            }
            
        }
    }
    @include phone{
        padding-right: 15px;
        padding-left: 15px;
        margin-bottom: 16px;
        .recommand{
            li{
                 width: 100%;
                 margin-bottom: 16px;
                .recommand_p h3{
                    margin-bottom: 16px;
                }
                .recommand_p :nth-child(2){
                    margin-bottom: 16px;
                }
             }
             
    
        }
        
    }
}
.recommand_mb{
    margin-bottom: 80px;
    @include pad{
        margin-bottom: 52px;

    }
}
/* 聯絡我們 */
.contact_us{
    p{ 
        width: 65.76%;
        margin: 0 auto;
        font-size: 20px;
        line-height: 30px;
        margin-bottom: 20px;
    }
    .contact_us_form {
        width: 65.76%;
        margin: 0 auto; 
        display: flex;
        flex-direction: column;
        input{
        
            margin-bottom: 16px;
            background: rgba(255, 255, 255, 0);
            
        }
        textarea{
            height: 136px;
            margin-bottom: 16px;
            background: rgba(255, 255, 255, 0);
        
        }
        .input_h{
            padding-top: 13px;
            padding-bottom: 13px;
        }
        .form_privacy {
            label{
                line-height: 24px;
                cursor: pointer;
            }
            margin-bottom: 26px;
        }
        .submit_button{
            text-align: center;
            input{
                width: 48%;
                padding-top: 16px;
                padding-bottom: 16px;
                background: #AA0601;
                border: none;
                cursor: pointer;
            
            }
            input:focus{
                background:  white;
                color: #AA0601;
                outline:1px solid  #AA0601;
            
            }

            
        }
    }
 
    @include pad{
            padding-bottom: 40px;
            font-size: 14px;
            line-height: 21px;
            p{
                font-size: 16px;
                line-height: 24px;
                margin-bottom: 12px;
                
            
            }

            .contact_us_form{
                width: 65.76%;
                .form_privacy{
                    margin-bottom: 20px;
                }
                input{
                    margin-bottom: 8px;
                    
                }
                textarea{
                    height: 100px;
                    margin-bottom: 8px;
                
                }
                .input_h{
                    padding-top: 7px;
                    padding-bottom: 7px;
                }
                .submit_button{
                    
                    input{
                        width: 46.66%;
                        padding-top: 8px;
                        padding-bottom: 8px;
                    }
                }
            }
    }
    @include phone{
            padding: 0 15px 32px;
            p{
                    width: 100%;
                    margin-bottom: 8px;
                    
                
            }
            .contact_us_form{
                width: 100%;
                .form_privacy{
                    margin-bottom: 24px;
                }
                textarea{
                        height: 100px;
                        margin-bottom: 8px;
                    
                    }
                .submit_button{
                        
                        input{
                            width: 47.82%;
                        }
                }
                
            }
    }
}


