*,*::before,*::after {
    box-sizing: border-box;
}
img{
    max-width: 100%;
    height: auto;
    vertical-align: middle;
}
  
/* ==================================== */
body{
    font-size: 16px;
    line-height: 24px;
    font-weight: bold;
    font-family: 'Noto Sans';
}
a{
    
    text-decoration: none;
}


.header a,.footer a {
    color: white;
}
.advantage,.contactUs{
    background: #AA06010D;
}
.header,.footer{
    background: #AA0601;
    font-weight: normal;
}
.container{
    max-width: 1110px;
    margin: 0 auto;
 
}
.twoside_padding{
    padding-left: 39px;
    padding-right: 39px;
}
.mb_18{
    margin-bottom: 18px;
}
.mb_80{
    margin-bottom: 80px;
}
.mr_12{
    margin-right: 12px;
}
.mr_30{
    margin-right: 30px;
}
.ml_30{
    margin-left: 30px;
}
.mr_38{
    margin-right: 38px;
}
.pb_80{
    padding-bottom: 80px;
}

.dflex_space_between_center{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.fz_64_lh_96{
    font-size: 64px;
    line-height: 96px;
}
.primary_color{
    color: #650300;
}
.secondary_color{
    color: white;
}
/* ===================================================*/
/* header */
.header{
    padding: 30px 56px 30px 88px;
    text-align: center;
    .logo {
        a{
            background: url(../images/logo.png) no-repeat;
            width: 80px;
            height: 49px;
            background-size: contain;
            display:block;
            text-indent: 101%;
            white-space: nowrap;
            overflow: hidden;
        }

    }
    .header_menu {
        a{
            padding: 10px 32px;
            font-size: 20px;
            line-height: 30px;
            display: block;  
        }
        a:hover{
            background: white;
            color: #AA0601 !important;
            
        }
        @include phone{
            width: 100%;
            flex-wrap: wrap;
            li{
                width: 50%;
                border: 1px solid white;
                display: block;
            }
            a{
                width: 100%;
                display: block;
            
            }
            a:hover{
                color: #AA0601;
                background: white;
            }
        }

    }
    @include pad{
        padding: 30px 19px 30px 39px;
        // a{
        //     padding: 0 20px;
                    
        //     }
    }
    @include phone{
        padding: 0;
        flex-direction: column;
        .logo{
            padding: 10px 0;
            a{
                width: 48px;
                height: 29px;
            }
        }
    }
    
    
}

/* footer */
.footer{
    padding: 0 39px;
    @include phone{
        padding: 0 15px;

    }
    .footer_content{
        align-items: flex-start;
            .contact_and_menu{
                padding-top: 48px;
                @include pad{
                    padding-top: 40px;
                    width: 62.61%;
                }
                @include phone{
                    padding-top: 24px;

                }
                .footer_menu{
                    margin-bottom: 36px;
                    a{
                        display: block;
                        padding: 5px;
                    }
                    a:hover{
                        background: white;
                        color: #AA0601 !important;
                        
                    }
                    @include pad{
                        width: 100%;
        
                    }
                    @include phone{
                        display: none;

                    }
                    
                }
                .contact_inf{
                    font-size: 36px;
                    line-height: 54px;
                    img{
                        width: 48px;
                        height: 48px;
                    }
                    a{
                        padding: 5px;
                    }
                    a:hover{
                        background: white;
                        color: #AA0601 !important;
                        
                    }
                    @include pad{
                        font-size: 20px;
                        line-height: 30px;  
                        img{
                            width: 32px;
                            height: 32px;
                        }
                    }
                    @include phone{
                        font-size: 16px;
                        li{
                            margin-bottom: 8px;
                        }
                        img{
                            width: 20px;
                            height: 20px;
                        }
                    }
                }
            
                
            }
            .sns{
                
                width: 18.74%;
                padding-top: 48px;
                a:hover{
                    background: none;

                }
                @include pad{
                    width: 15.625%;
                    padding-top: 40px;
                    img{
                        width: 24px;
                        height: 24px;
                    }
                }
                @include phone{
                    width: 34.83%;
                    padding-top: 24px;
                }
            
            }
    }

    .service{
        padding: 24px 0;
        border-top: 1px solid white;
        @include pad{
            .foote_logo{
                display: none;
            }
        }
        @include phone{
            padding-top: 18px;
            flex-direction: row-reverse;
            align-items: flex-end;
            padding: 18px 0 21px;           
            .foote_logo{
                margin: 0;
                display: block;
                width: 46px;
            }
        }
        .service_content{
            width: 89.37%;
            p{
                width: 50%;
            }
            @include pad{
                width: 100%;
                p{
                    width: 46.66%;
                }
            }
            @include phone{
                flex-wrap: wrap;
                p{
                    margin-bottom: 7px;
                    width: 100%;
                    flex-wrap: wrap;
                    font-size: 14px;
                    line-height: 21px;
                }

            }

            .service_law{
                width: 15.12%; 
                font-size: 14px;
                line-height: 21px;
                a{
                    display: block;
                    padding: 5px;
                }
                a:hover{
                    background: white;
                    color: #AA0601 !important;
                    
                }
                @include pad{
                    width: 21.74%;
    
                }
                @include phone{
                    width: 100%;
                    flex-wrap: wrap;
                    font-size: 14px;
                    line-height: 21px;
                    li{
                        width: 100%;
                        flex-wrap: wrap;
                        margin-bottom: 7px;
                    }
                }
             }
        }

    
    }
}
